
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap');

*{
    box-sizing: border-box;
    font-family: 'Poppins';
    /* margin: 0; */
}

input{
    color: grey;
    font-size: 1.2rem;
    width: 100%;
    margin-bottom: 20px;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 7px;
    margin: 20px;
    overflow: hidden;
}

.card p {
    padding: 5px;
}
.cardGrid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
